import { handleApi } from "@/utils/baseService";
import {
  callApiLayDanhSachDonViBaoCao,
  callApiLayDanhSachChatLuongKhongKhi,
} from "@/api/home-reports";

const layDanhSachDonViBaoCao = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(
      resolve,
      reject,
      context,
      "SET_DANH_SACH_DON_VI_BAO_CAO",
      callApiLayDanhSachDonViBaoCao(payload)
    );
  });
};

const layDanhSachChatLuongKhongKhi = (context, payload) => {
  return new Promise((resolve, reject) => {
    handleApi(
      resolve,
      reject,
      context,
      "SET_DANH_SACH_CHAT_LUONG_KHONG_KHI",
      callApiLayDanhSachChatLuongKhongKhi(payload)
    );
  });
};

export default {
  layDanhSachDonViBaoCao,
  layDanhSachChatLuongKhongKhi,
};
