export function callApiLayDanhSachDonViBaoCao() {
  return new Promise((resolve, reject) => {
    return HTTP.get(ENDPOINT.HOME_REPORTS.UNIT_REPORT)
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

export function callApiLayDanhSachChatLuongKhongKhi() {
  return new Promise((resolve, reject) => {
    return HTTP_CHAT_LUONG_KHONG_KHI.post(
      ENDPOINT.HOME_REPORTS.CHAT_LUONG_KHONG_KHI
    )
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}
